
.ethfans {
  .landing-content {
    display: flex;
    width: 1180px;
    margin: 0 auto;
  }

  .sidebar {
    margin-left: 16px;

    header {
      padding: .5em 1em;
      border-bottom: 1px solid $ethfans-dividers;
      color: $ethfans-secondary-text;
      font-size: 13px;
    }

    .hot-topic {
      width: 308px;
      border-radius: 2px;
      background-color: #fff;
      box-shadow: 0 0 1px rgba(0,0,0,.15);
      margin-bottom: 1em;

      .item {
        display: flex;
        align-items: center;
        padding: 1em;
        border-bottom: 1px solid $ethfans-dividers;

        .index {
          @include border-round(25px);
          display: inline-block;
          border: 1px solid $ethfans-secondary-text;
          color: $ethfans-secondary-text;
          text-align: center;
          line-height: 23px;
          font-size: 12px;
        }

        .meta {
          width: 75%;
          margin-left: 1em;

          .title {
            @include line-clamp(2);
            color: $ethfans-primary;
            font-size: 13px;
            line-height: 1.3;

            &:hover {
              text-decoration: underline;
            }
          }

          .name {
            font-size: 12px;
            color: $ethfans-secondary-text;
          }
        }
      }
    }
  }
}
