$ethfans-primary: #343536;
$ethfans-primary-text: lighten($ethfans-primary, 10%);
$ethfans-secondary-text: lighten($ethfans-primary-text, 30%);
$ethfans-dividers: lighten($ethfans-primary, 70%);
$ethfans-bg: #ECF0F1;

body {
  background-color: $ethfans-bg;
}

.article {
  width: 100%;
}

.ethfans {
  .navbar-wrapper {
    width: 100%;
    height: 80px;
    margin-bottom: 2em;
    background: rgba(255,255,255,.97);
    box-shadow: 0 0 1px rgba(0,0,0,.15);
    line-height: 80px;
  }
  .navbar {
    position: relative;
    width: 1180px;
    margin: 0 auto;

    &.search-focus {
      .navbar-main .nav {
        opacity: 0;
      }

      .navbar-aside .search {
        width: 920px;
        padding: $input-padding-y $input-padding-x;
        color: $ethfans-primary;
      }
    }

    .navbar-main {
      display: flex;

      .logo {
        margin: 0;
        line-height: 80px;
        color: $ethfans-primary;
      }

      .nav {
        margin-left: 2em;
        transition: opacity .5s;

        a {
          margin-left: 1.5em;
          color: $ethfans-secondary-text;
          transition: color .5s;

          &.active,
          &:hover {
            color: $ethfans-primary-text;
          }

          &:first-child {
            margin-left: 0;
          }
        }
      }
    }

    .navbar-aside {
      position: absolute;
      top: 0;
      right: 0;
      display: flex;

      .icon-search {
        width: 24px;
        height: 24px;
        margin-top: 28px;
        fill: $ethfans-secondary-text;
        cursor: pointer;
      }

      .search {
        @include form-control-size(0, 80px, $input-padding-y, $input-padding-x, $font-size-small, $input-line-height);
        @include form-control-variant($input-color, none, #fff);
        transform: translateY(-2px);
        transition: all .8s ease-in-out;
        box-sizing: border-box;
        padding: 0;
      }

      .entry {
        margin-left: 2em;
      }
    }
  }
}
